  font-weight: 900;
  color: #000;
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .tagline {
    font-size: 3rem;
  }
}

.descripcion-pagina {
  text-align: center;
}

.formulario {
  margin: 5rem 0;
}
.formulario .campo {
  display: flex;
  margin: 2rem 0;
  align-items: center;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  border: none;
  padding: 1rem;
  height: 4rem;
  background-color: #d8dadf;
}

.acciones {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .acciones {
    font-size: 1.6rem;
    flex-direction: row;
    justify-content: space-between;
  }
}
.acciones p {
  margin: 0;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .acciones p {
    font-size: 1.6rem;
  }
}
.acciones a {
  font-weight: bold;
}
.acciones a:hover {
  color: #a25de1;
}

.alertas {
  margin: 2rem 0;
  padding: 1rem 2rem;
  text-transform: uppercase;
  font-size: 1.3rem;
}
.alertas.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alertas.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.swal2-popup {
  font-size: 1.2rem !important;
}
@media (min-width: 768px) {
  .swal2-popup {
    font-size: 1.5rem !important;
  }
}

