    height: 100vh;
  }
}
.dashboard .principal {
  width: 100%;
  overflow-x: hidden;
}
.dashboard .principal .contenido h3 {
  font-size: 3rem;
  text-align: center;
  margin: 0;
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .dashboard .principal .contenido h3 {
    font-size: 4rem;
  }
}
