  padding: 0 3rem;
}
.dashboard .principal .contenido .contenedor-sm .formulario {
  margin: 3rem 0;
}
.dashboard .principal .contenido .contenedor-sm .formulario .campo label {
  flex: 0 0 8rem;
}
@media (min-width: 768px) {
