.sidebar {
  height: 0;
  opacity: 0;
  background-color: #4338CA;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: opacity 0.5s ease;
  transform: translateX(-100%);
  visibility: hidden;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: 100%;
    width: 35rem;
    gap: 3rem;
    transform: translateX(0);
    visibility: visible;
  }
}
.sidebar h2 {
  margin: 3rem;
  font-size: 6rem;
}
.sidebar ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin-top: 5rem;
  gap: 3rem;
}
@media (min-width: 768px) {
  .sidebar ul {
    width: 100%;
    padding: 0 2rem;
    align-items: flex-start;
    gap: 2rem;
  }
}
.sidebar ul li {
  list-style: none;
  text-transform: uppercase;
  width: 100%;
  transition: all 0.3s;
}
.sidebar ul li .enlace {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  border-radius: 1rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .sidebar ul li .enlace {
    font-size: 1.5rem;
    justify-content: flex-start;
    padding-left: 1rem;
  }
}
.sidebar ul li .enlace:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.sidebar ul li .activo {
  background-color: #28217b;
